.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  padding-top: 15%;
  background-color: #282c34;
  min-height: 100vh;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: black;
  margin: auto;
  width: 50%;
}

.App-header-h2 {
  color: white;
}

.App-link {
  color: #61dafb;
}

._status-chat-widget {
  background-color: white;
  max-width: 850px;
  border-radius: 16px;
  font-family: "Inter UI",-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif;
  background: url(https://status.im/img/status-logo-symbol.svg) 99% 3% no-repeat white;
  background-size: 55px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

._status-chat-widget h3 {
  margin-top: 0px;
  color: #4360df;
  font-weight: 600;
  font-size: 20px;
  padding: 24px;
  padding-bottom: 5px;
}

._status-chat-widget .chat {
  height: 218px;
  overflow: auto;
  padding: 24px;
  border-top: 1px solid #ddd;
  background-color: #eef2f5;

  max-width: 850px;
}

._status-chat-widget .post {
  width: 100%;
  height: 60px;
  padding-left: 30px;
  border-radius: 0px 0px 10px 10px;
  border: 0px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  font-size: 14px;
}

._status-chat-widget .identicon {
  border-radius: 50%;
  float: left;
  margin-top: 10px;
}

._status-chat-widget .username {
  color: #939ba1;
  font-size: 14px;
  margin-left: 20px;
}

._status-chat-widget .message {
  background-color: white;
  padding: 5px 10px;
  border-radius: 10px;
  border: 1px solid #eee;
  display: block;
  margin-top: 3px;
  margin-bottom: 10px;
  margin-left: 60px;

  max-width: 850px;
}